/*
	global constants 
	* should be in their own page, with other config/device variables
	* we will change these variables per device, and they will alter the functionality of the app, without us having to actually edit the codebase
	* this will be useful to enable/disable modules in similar devices which will run a compatible version of the app
*/
import * as deviceJSON from "@src/device.json";
window.device = {};
window.device.info = deviceJSON.info;

window.device.routes = [];
deviceJSON.pages.map(function(one,two){
	window.device.routes.push(one);
	return true;
});

window.device.routes.login = {
	url:"/login",
	component:"user/PageLogin",
	title:"Login",
	icon:"icon-account",
};
window.device.routes.pageNotFound = {
	url:"*",
	component:"000/PageNotFound",
	title:"Page Not Found",
	icon:"icon-account",
	redirect: "/login"
};


/*
	app
*/
import React from 'react';
import ReactDOM from 'react-dom';
import { Router, Route, Redirect, browserHistory } from 'react-router';
import { Provider } from 'react-redux';


/*
	log
*/
if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
	window.log = function(){
		let args = Array.from(arguments);
		/*
			theme
		*/
		var log = {
			titleColor: "rgba(255,255,255,0.75)",
			textColor: "rgba(0,0,0,0.75)",
			bgColor: "rgb(30,30,30)"
		}
		switch (args[0]) {
			case "action success":
				log.bgColor = "rgb(255,255,255)";
				log.titleColor = "green";
				log.textColor = "darkgreen";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "action fail":
				log.bgColor = "rgb(255,255,255)";
				log.titleColor = "red";
				log.textColor = "darkred";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "action":
				log.bgColor = "rgb(255,255,255)";
				log.titleColor = "darkgrey";
				log.textColor = "black";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "error":
				log.titleColor = "Tomato";
				log.textColor = "red";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "warning":
				log.titleColor = "lightorange";
				log.textColor = "darkorange";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "success":
				log.titleColor = "lightgreen";
				log.textColor = "darkgreen";
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			case "log":
				if (args[1]) {
					args = args.slice(1);
				}
			break;
			default:
			break;
		}
		/*
			log
		*/
		for (var i = 0; i < args.length; i++) {
			if (i===0) {
				if (typeof args[0]==="string") {
					if (args[1]) {
						// groupStart
						window.console.groupCollapsed("%c " + args[i] + " ", 'background:'+log.bgColor+';color:'+log.titleColor+';padding:1px 0;');
					} else {
						// log
						console.log("%c " + args[i] + " ", 'background:'+log.bgColor+';color:'+log.titleColor+';padding:1px 0;');
					}
				}
			} else if (args[i]) {
				if (args[i].length>1 && (typeof args[0]==="string") && (typeof args[1]==="object")) {
					// group (mimick redux logger "prev state" "action" "next state" logging)
					console.log("%c " + args[i][0], "color:darkgrey;", args[i][1]);
				} else {
					// normal
					console.log(args[i]);
				}
			}
		}
		// groupEnd
		if (typeof args[0]==="string" && args[1]) {
			window.console.groupEnd();
		}
	}
} else {
	window.log = function(){
	};
}


/*
	store
	middleware
*/
import configureStore from '@src/store';
const store = configureStore(browserHistory);


/*
	style
*/
import '@src/style/index.scss';
import '@src/components/form/luxulForm.scss';
import '@src/components/form/luxulTable.scss';


/*
	route
	dynamic from window.device.routes, also some manual routes such as "login" and "page not found"
*/
import * as layoutActions from '@src/states/layout/actions';
const routeOnEnter = ()=>{
	store.dispatch(layoutActions.message({}));
};
const Routes = [];
var routeComponent;

// default route
for (let i = 0; i < window.device.routes.length; i++) {
	if (!window.defaultRoute) {
		window.defaultRoute = window.device.routes[i];
		Routes.push(<Redirect key="routeRedirect" from="/" to={window.defaultRoute.url} />);
		break;
	}
}

// login route
routeComponent = require('./pages/'+window.device.routes.login.component);
if (routeComponent.default) {
	Routes.push(<Route key="routeLogin" path={window.device.routes.login.url} component={routeComponent.default} page={window.device.routes.login} onEnter={routeOnEnter} />);
}

// modules
for (let i = 0; i < window.device.routes.length; i++) {
	// 1st level nav/url
	// /example: quicksetup
	if (window.device.routes[i].component) {
		Routes.push(<Route key={"route"+i} path={window.device.routes[i].url} component={function(i){ return require('./pages/'+window.device.routes[i].component).default; }(i)} page={window.device.routes[i]} onEnter={routeOnEnter} />);
	}
	if (window.device.routes[i].children) {
		for (let ii = 0; ii < window.device.routes[i].children.length; ii++) {
			// 2nd level nav/url routes
			// example: /wifi/connect
			if (window.device.routes[i].children[ii].component) {
				Routes.push(<Route key={"route"+i+"."+ii} path={window.device.routes[i].children[ii].url} component={function(i, ii){ return require('./pages/'+window.device.routes[i].children[ii].component).default; }(i, ii)} page={window.device.routes[i].children[ii]} onEnter={routeOnEnter} />);
			}
			if (window.device.routes[i].children[ii].children) {
				for (let iii = 0; iii < window.device.routes[i].children[ii].children.length; iii++) {
					// 3rd level nav/url routes
					// example: /x_network/multiwan/options
					if (window.device.routes[i].children[ii].children[iii].component) {
						Routes.push(<Route key={"route"+i+"."+ii} path={window.device.routes[i].children[ii].children[iii].url} component={function(i, ii, iii){ return require('./pages/'+window.device.routes[i].children[ii].children[iii].component).default; }(i, ii, iii)} page={window.device.routes[i].children[ii].children[iii]} onEnter={routeOnEnter} />);
					}
				}
			}
		}
	}
}

// generic modules, to be used for all devices always
routeComponent = require('./pages/'+window.device.routes.pageNotFound.component);
if (routeComponent.default) {
	Routes.push(<Route key="routepageNotFound" path={window.device.routes.pageNotFound.url} component={routeComponent.default} page={window.device.routes.pageNotFound} onEnter={routeOnEnter} />);
}


/*
	go
*/
// injectTapEventPlugin();
ReactDOM.render(
	<Provider store={store}>
		<Router history={browserHistory}>
			{Routes}
		</Router>
	</Provider>,
	document.getElementById('app')
);








